@import url(//cdn.muicss.com/mui-0.10.0/css/mui.min.css);
@import url(//cdn.muicss.com/mui-0.10.0/extra/mui-colors.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
textarea {
  min-height: 100px;
  width: 200px;
}

.postcontainer {
  background-color: rgb(231, 230, 230);
}
.profilepic {
  width: 150px;
  height: 150px;

  border-radius: 90px;
}
img {
  width: 100%;
}

footer {
  min-height: 200px;
  background-color: #282c34;
}
footer > p {
  color: white;
}
p {
  font-size: 1.2rem;
}
li {
  list-style-type: none;
}
.newcolor {
  background-color: rgb(42, 247, 10);
}

